.wrp {
  background-color: rgba(0, 0, 0, 0.3); }

.menu {
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none; }
  .menu__item__link {
    display: block;
    color: #fff;
    padding: 20px 25px;
    text-transform: uppercase; }
    .menu__item__link:hover {
      color: #ce1f11;
      text-decoration: none; }

.drop {
  width: auto; }
  .drop .dropMenu {
    background-color: rgba(0, 0, 0, 0.3);
    list-style: none;
    padding: 25px;
    display: grid;
    grid-template-columns: repeat(5, 1fr); }
    .drop .dropMenu .menu__item__link {
      padding: 5px; }
